import './styles.css';
import sunnyImage from './images/sunny2.png';
import rainyImage from './images/rainy.png';
import cloudyImage from './images/cloudy.png';

const apiKey = 'PCCNYN8UV94664GZJFBCJV5NV'; 
const apiUrl = 'https://weather.visualcrossing.com/VisualCrossingWebServices/rest/services/timeline/';
let isCelsius = true;

async function fetchWeather(location) {
    try {
        const response = await fetch(`${apiUrl}${location}?key=${apiKey}`);
        const data = await response.json();
        
        if (data && data.currentConditions) {
            const currentWeather = data.currentConditions;
            displayWeather(currentWeather); 
        } else {
            alert('Weather data not found!');
        }
    } catch (error) {
        console.error('Error fetching weather data:', error);
        alert('Failed to fetch weather data.');
    } finally {
        document.getElementById('loading').style.display = 'none';
    }
}

function displayWeather(weatherData) {
    let temperature = weatherData.temp;
    let tempUnit = "°F"; 
    if (isCelsius) {
        temperature = (temperature - 32) * 5 / 9;
        tempUnit = "°C"; 
    }

    const weatherDescription = weatherData.conditions.toLowerCase();
    const backgroundColor = getBackgroundColor(weatherDescription);

    const weatherInfoElement = document.getElementById('weather-info');
    weatherInfoElement.innerHTML = ''; 

    weatherInfoElement.innerHTML = `
        <h2>${weatherData.conditions}</h2>
        <p>Temperature: ${temperature.toFixed(1)} ${tempUnit}</p>
        <p>Humidity: ${weatherData.humidity}%</p>
        <p>Wind: ${weatherData.windspeed} km/h</p>
    `;

    document.body.style.backgroundColor = backgroundColor;

    const weatherImage = getWeatherImage(weatherDescription);
    if (weatherImage) {
        const imageElement = document.createElement('img');
        imageElement.src = weatherImage;
        imageElement.alt = 'Weather image';
        weatherInfoElement.appendChild(imageElement); 
    }
}

function getBackgroundColor(weatherDescription) {
    if (weatherDescription.includes('sun')) {
        return 'yellow';
    } else if (weatherDescription.includes('rain')) {
        return 'lightblue';
    } else if (weatherDescription.includes('cloud')) {
        return '#f0f0f0';
    }
    return '#ffffff'; 
}

function getWeatherImage(weatherDescription) {
    if (weatherDescription.includes('sun') || weatherDescription.includes('clear')) {
        return sunnyImage; 
    } else if (weatherDescription.includes('rain'))  {
        return rainyImage;
    } else if (weatherDescription.includes('cloud') || weatherDescription.includes('overcast')) {
        return cloudyImage;
    }
    return ''; 
}

document.getElementById('toggle-temp').addEventListener('click', () => {
    isCelsius = !isCelsius;
    const location = document.getElementById('location').value;
    if (location) {
        document.getElementById('loading').style.display = 'block';
        fetchWeather(location); 
    }
});

document.getElementById('weather-form').addEventListener('submit', (e) => {
    e.preventDefault();
    const location = document.getElementById('location').value;
    if (location) {
        document.getElementById('loading').style.display = 'block';
        fetchWeather(location);
    }
});